// Initial State
const initialState = {
  loading: true,
  pops: [],
  tops: [],
  ups: [],
  gens: []
};

// Redux: Counter Reducer
const counterReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'UPDATE_POPS': {
      return {
        ...state,
        pops: state.pops.concat(action.value),
      };
    }
    case 'UPDATE_TOPS': {
      return {
        ...state,
        tops: state.tops.concat(action.value),
      };
    }
    case 'UPDATE_UPS': {
      return {
        ...state,
        ups: state.ups.concat(action.value),
      };
    }
    case 'UPDATE_GENS': {
      return {
        ...state,
        gens: action.value,
      };
    }
    case 'UPDATE_LOADING': {
      return {
        ...state,
        loading: action.value,
      };
    }
    default: {
      return state;
    }
  }
};

// Exports
export default counterReducer;