function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min)) + min;
}

function tenTimes () {
  const results = [uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4(), uuidv4()]
  return results.join('\n');
}

