  getNextRndGram = (grams, name) => {
    const arr = grams[name] || grams['^']
    const max = arr.length;
    const rnd = getRandomInt(0, max - 1);
    const f1 = arr[rnd] || '^';
    return f1 !== name ? f1 : '^';
  }

  getLastTwoWordsGram = (gram) => {
    const lt = gram.slice(-3) || '';
    return '^' + lt.replace(/[\^]/g, '').toLowerCase();
  }

  generateName = () => {
    let r = '';
    let f = '';
    let l2f = '';
    for (let index = 0; index < 8; index++) {
      f = this.getNextRndGram(this.state.grams, l2f);
      l2f = this.getLastTwoWordsGram(f);
      r += f
    }
    return (r).replace(/[\^]/g, '');
  }

